<template>
	<div class="CheckOrder_detail">
		<header><i class="iconfont icon-xiangzuo1" @click="toReceipt"></i><span>按单复核-查看明细</span></header>
		<div class="container">
			<table cellspacing="0">
				<thead>
					<tr>
						<td>序号</td>
						<td  @click="SortArr('CheckOrder_detailData','Sku','SortCondition1',$event)">商品代码</td>
						<td  @click="SortArr('CheckOrder_detailData','Qty','SortCondition2',$event)">数量</td>
						<td  @click="SortArr('CheckOrder_detailData','CheckedQty','SortCondition3',$event)">已复核数</td>
						<td  @click="SortArr('CheckOrder_detailData','Descr','SortCondition4',$event)">商品名称</td>
						<td  @click="SortArr('CheckOrder_detailData','Susr2','SortCondition5',$event)">单位</td>
						<td  @click="SortArr('CheckOrder_detailData','PreCheckQty','SortCondition6',$event)">待复核数</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in CheckOrder_detailData'  :id="index"  :key='index'  @click="">
						<td>{{index+1}}</td>
						<td>{{dataItem.Sku}}</td>
						<td>{{dataItem.Qty}}</td>
						<td>{{dataItem.CheckedQty}}</td>
						<td>{{dataItem.Descr}}</td>
						<td>{{dataItem.Susr2}}</td>
						<td>{{dataItem.PreCheckQty}}</td>
					</tr>
				</tbody>
			</table>
			<div class="kong"></div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"CheckOrder_detail",
		data(){
			return{
				CheckOrder_detailData:[],//列表数据
				OrderKey:'',
				SortCondition1:true,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
				SortCondition5:false,
				SortCondition6:false,
			}
		},
		mounted(){
			$("thead").find("td").append('<i class="iconfont"></i>')
		},
		created(){
			// SO单号
			if(this.$route.query.OrderKey){

				this.OrderKey = this.$route.query.OrderKey;
			}
			
			console.log(this.$store.state.CheckOrder_detailData)

			if(this.$store.state.CheckOrder_detailData.length){

				var CheckOrder_detailData = this.$store.state.CheckOrder_detailData;

				this.CheckOrder_detailData = CheckOrder_detailData;

			}
		},
		methods:{
			// 返回上一页 录入单号 页面
			toReceipt:function(){

				var that = this;

				this.$router.push({
					name:"CheckOrder",
					query:{
						OrderKey:that.OrderKey,
					}
				})
			},
		}
	}
</script>
<style lang="less" scoped>
	.CheckOrder_detail{
		&>.container{
			width:100%;
			table{
				tr{
					td:nth-child(2){
						min-width: 200/50rem;
					}
					td:nth-child(3){
						min-width: 150/50rem;
					}
					td:nth-child(4),td:nth-child(7){
						min-width: 200/50rem;
					}
					td:nth-child(5){
						min-width: 350/50rem;
					}
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}	
		}
	}
	@media screen and (max-width: 250px){
		.CheckOrder_detail{
			&>.container{
				table{
					tr{
						td:nth-child(2){
							min-width: 200/50rem;
						}
						td:nth-child(3){
							min-width: 200/50rem;
						}
						td:nth-child(4),td:nth-child(7){
							min-width: 260/50rem;
						}
						td:nth-child(5){
							min-width: 500/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
</style>


